import LoadingImg from '../public/loadingImg'
  import Bus from '../assets/eventBus'
	import Head from "../public/head.vue"
	import Experiencenav from "./experience/experienceNav"
	export default {
		data() {
			return {
			  api: this.GLOBAL.baseURL,
				navindex:"",
				topData:"",
        num:15,
        stepNum:'',
        overType:'',
        isCheck:true,
        price:'',
        priceall:'',
        personNumber:'',
        data:'',
        userImg:'',
        time:'',
        server_fee:'',
        getMoney:'',
        prideNum:'',
        howprice:'',
        activeId:'',
        isLookPrice:false,
        priceReg: /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/,
        reg: /^[1-9]\d*$/,
         Oreg:/^0|0[.]?[0]∗0/
			};
		},
		components:{
			Head,
      Experiencenav,
      LoadingImg
		},
		methods:{
      lookPrice(){
        this.isLookPrice = !this.isLookPrice
      },
			changenavf(msg){
        this.navindex=msg;
        localStorage.setItem('navIndex',msg)
			},
			changeRouter(msg){
          this.navindex=msg.id;
          this.$router.push({
            name: msg.router,
            query:{
              information: msg.information,
              complete: msg.complete
            }
          });
          this.overType = msg.id - 1
          this.step()
			},
      step(){
        if(this.activeId){
          this.$http.post(this.api + '/ActivityE',{
            token: localStorage.getItem('token'),
            activity_id:  this.activeId,
            visit:0
          })
            .then(res=>{
              if(res.data.code == 1){
                var arr = res.data.data.step.replace(/[^0-9]/ig,' ').trim().split(/\s+/)
                // this.navindex = arr[arr.length-1] 
                Bus.$emit('change',arr)
                let a = arr.length
                if(a<15){
                  this.isCheck = true
                  this.num = 15 - a
                
                  this.stepNum = Number(a/15*100).toFixed(2) + '%'
                }else{
                  this.isCheck = false
                  this.stepNum = Number(a/15*100).toFixed(2) + '%'
                }
                this.data = res.data.data
                this.time= res.data.data.total_time

              }else if(res.data.code == 3){
                this.step()
              }else if(res.data.code == 0){
                this.$alert(res.data.msg)
              }
            })
        }else{
          this.stepNum = 0
        }
      },
      getConfig(){
			  this.$http.post(this.api + '/Config',{
			    token: localStorage.getItem('token')
        })
          .then(res=>{
            if(res.data.code == 1){
              this.server_fee = res.data.data.server_fee + '%'
            }else if(res.data.code == 3){
              this.getConfig()
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      saveActive(msg){
			  this.activeId = msg
      },
      padunNov(){

      }
		},
    watch:{
      price:function(){
        this.priceall = this.price * 3
      },
      howprice:function(){
        if(this.Oreg.test(this.howprice)&&this.howprice||this.howprice && !this.priceReg.test(this.howprice)){
          this.$message({
            type: 'error',
            message: '请输入正确金额(大于1最多保留两位小数)'
          })
        }else if(this.prideNum && this.howprice){
          this.getMoney =parseInt(this.prideNum) * parseInt(this.howprice) - parseInt(this.prideNum) * parseInt(this.howprice)*(this.server_fee.replace("%","")/100)
        }
      },
      prideNum:function(){
        if(this.prideNum && !this.reg.test(this.prideNum)){
          this.$message({
            type: 'error',
            message: '请输入正确数量!'
          })
        }else if(this.prideNum && this.howprice){
          this.getMoney = parseInt(this.prideNum) * parseInt(this.howprice) -parseInt(this.prideNum) * parseInt(this.howprice)* (this.server_fee.replace("%","")/100)
        }
      },
      activeId:function(){
        Bus.$emit('changeAC',this.activeId)
      }
    },
		mounted(){
		  let _this = this
      if(localStorage.getItem('userImg')){
        this.userImg = localStorage.getItem('userImg')
      }else{
        this.userImg = '../../../static/img/static/user.png'
      }
      if(localStorage.getItem('navIndex')){
        this.navindex = localStorage.getItem('navIndex')
      }
			_this.step()
      _this.getConfig()
		},
		destroyed(){
      localStorage.removeItem('complete')
      localStorage.removeItem('navIndex')
		},
    created() {
		  this.activeId = this.$route.query.information
    }
  }



